<template>
  <div id="articleComponent">   
    <Menu mode="horizontal" :theme="theme" active-name="1" @on-select="changeArticle">
        <MenuItem name="1">
            <Icon type="ios-paper"></Icon>
            文章管理
        </MenuItem>
        <MenuItem name="2">
            <Icon type="ios-people"></Icon>
            标签管理
        </MenuItem>        
    </Menu>  
    <div>
        <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
    name: 'articleComponent',
    data(){
      return {
        theme: 'dark',
        routers: ['/back/article','/back/article/tag']       
      }
    },
    methods:{
        changeArticle(name){
            this.$router.push(this.routers[parseInt(name)-1]);
        }
    }
}
</script>
<style lang="scss" scoped>

</style>



